.MenuToggle{
    position: fixed;
    top: 40px;
    left: 40px;
    font-size: 20;
    cursor: pointer;
    color: #fff;
    -webkit-transition: all .22s ease-in;
    -o-transition: all .22s ease-in;
    transition: all .22s ease-in;
    z-index: 100;
}
.MenuToggle:hover{
    opacity: .7;
}

.MenuToggle.open{
    left: 320px;
}


